<template>
  <div class="memory-game">
    <div class="container">
      <h1>ÁøªÁøª‰πê</h1>
      <div class="game-info">
        <div class="moves">Ê≠•Êï∞: {{ moves }}</div>
        <div class="pairs">ÈÖçÂØπ: {{ matchedPairs }}/{{ totalPairs }}</div>
        <div class="controls">
          <button @click="initGame" class="restart-btn">ÈáçÊñ∞ÂºÄÂßã</button>
        </div>
      </div>
      
      <div class="game-board">
        <div 
          v-for="(row, rowIndex) in board" 
          :key="rowIndex"
          class="board-row"
        >
          <div 
            v-for="(card, colIndex) in row" 
            :key="colIndex"
            class="card"
            :class="{ 
              'flipped': card.isFlipped,
              'matched': card.isMatched
            }"
            @click="flipCard(rowIndex, colIndex)"
          >
            <div class="card-face card-front"></div>
            <div class="card-face card-back">
              <span class="emoji">{{ card.emoji }}</span>
            </div>
          </div>
        </div>
      </div>
      
      <div v-if="gameCompleted" class="game-completed">
        <h2>ÊÅ≠Âñú‰Ω†ÂÆåÊàê‰∫ÜÊ∏∏ÊàèÔºÅ</h2>
        <p>ÊÄªÂÖ±Áî®‰∫Ü {{ moves }} Ê≠•</p>
        <button @click="initGame" class="play-again-btn">ÂÜçÁé©‰∏ÄÊ¨°</button>
      </div>
    </div>
  </div>
</template>

<script setup lang="ts">
import { ref, reactive, onMounted } from 'vue'

// ÂÆö‰πâÁªÑ‰ª∂ÂêçÁß∞
defineOptions({
  name: 'MemoryGame'
})

// Ê∏∏ÊàèÂ∏∏Èáè
const BOARD_SIZE = 4
const EMOJIS: string[] = ['üòÄ', 'üòÇ', 'ü•∞', 'üòé', 'ü§©', 'ü•≥', 'üòç', 'ü§ó']

// Âç°ÁâáÊé•Âè£
interface Card {
  emoji: string
  isFlipped: boolean
  isMatched: boolean
}

// Ê∏∏ÊàèÁä∂ÊÄÅ
const board = reactive<Card[][]>([])
const moves = ref(0)
const matchedPairs = ref(0)
const totalPairs = ref(0)
const gameCompleted = ref(false)
const firstCard = ref<{ row: number; col: number } | null>(null)
const secondCard = ref<{ row: number; col: number } | null>(null)
const lockBoard = ref(false)

// ÂàùÂßãÂåñÊ∏∏Êàè
const initGame = () => {
  // ÈáçÁΩÆÊ∏∏ÊàèÁä∂ÊÄÅ
  moves.value = 0
  matchedPairs.value = 0
  gameCompleted.value = false
  firstCard.value = null
  secondCard.value = null
  lockBoard.value = false
  
  // Ê∏ÖÁ©∫Ê∏∏ÊàèÊùø
  board.length = 0
  
  // ËÆ°ÁÆóÈÖçÂØπÊï∞Èáè
  totalPairs.value = (BOARD_SIZE * BOARD_SIZE) / 2
  
  // ÂàõÂª∫Âç°ÁâáÊï∞ÁªÑ
  const emojis: string[] = []
  
  // ÈÄâÊã©ÈúÄË¶ÅÁöÑemojiÊï∞Èáè
  for (let i = 0; i < totalPairs.value; i++) {
    // Á°Æ‰øùEMOJIS[i]Â≠òÂú®
    if (EMOJIS[i] !== undefined) {
      // @ts-expect-error Á°Æ‰øùEMOJIS[i]Â≠òÂú®
      emojis.push(EMOJIS[i])
      // @ts-expect-error Á°Æ‰øùEMOJIS[i]Â≠òÂú®
      emojis.push(EMOJIS[i])
    }
  }
  
  // ÈöèÊú∫Êâì‰π±emojiÊï∞ÁªÑ
  shuffleArray(emojis)
  
  // ÂàõÂª∫Ê∏∏ÊàèÊùø
  let emojiIndex = 0
  for (let row = 0; row < BOARD_SIZE; row++) {
    const rowCards: Card[] = []
    for (let col = 0; col < BOARD_SIZE; col++) {
      // Á°Æ‰øùemojis[emojiIndex]Â≠òÂú®
      if (emojis[emojiIndex] !== undefined) {
        rowCards.push({
          emoji: emojis[emojiIndex]!,
          isFlipped: false,
          isMatched: false
        })
      }
      emojiIndex++
    }
    board.push(rowCards)
  }
}

// ÈöèÊú∫Êâì‰π±Êï∞ÁªÑ
const shuffleArray = (array: string[]) => {
  for (let i = array.length - 1; i > 0; i--) {
    const j = Math.floor(Math.random() * (i + 1));
    // ‰ΩøÁî®ÈùûÁ©∫Êñ≠Ë®ÄÔºåÂõ†‰∏∫ÂâçÈù¢Â∑≤ÁªèÊ£ÄÊü•‰∫Üundefined
    if (array[i] !== undefined && array[j] !== undefined) {
      [array[i], array[j]] = [array[j]!, array[i]!]
    }
  }
}

// ÁøªÁâå
const flipCard = (row: number, col: number) => {
  // Â¶ÇÊûúÊ∏∏ÊàèÈîÅÂÆö„ÄÅÂç°ÁâáÂ∑≤ÁøªÂºÄÊàñÂ∑≤ÈÖçÂØπÔºåÂàô‰∏çÂ§ÑÁêÜ
  const card = board[row]?.[col]
  if (lockBoard.value || !card || card.isFlipped || card.isMatched) {
    return
  }
  
  // ÁøªÂºÄÂç°Áâá
  if (card) {
    card.isFlipped = true
  }
  
  // Â¶ÇÊûúËøôÊòØÁ¨¨‰∏ÄÂº†ÁøªÂºÄÁöÑÂç°Áâá
  if (!firstCard.value) {
    firstCard.value = { row, col }
    return
  }
  
  // ËøôÊòØÁ¨¨‰∫åÂº†ÁøªÂºÄÁöÑÂç°Áâá
  secondCard.value = { row, col }
  
  // Â¢ûÂä†Ê≠•Êï∞
  moves.value++
  
  // Ê£ÄÊü•ÊòØÂê¶ÂåπÈÖç
  checkForMatch()
}

// Ê£ÄÊü•ÊòØÂê¶ÂåπÈÖç
const checkForMatch = () => {
  if (!firstCard.value || !secondCard.value) return
  
  // @ts-expect-error Á°Æ‰øùËÆøÈóÆÊòØÂÆâÂÖ®ÁöÑ
  const firstCardObj = board[firstCard.value.row][firstCard.value.col]
  // @ts-expect-error Á°Æ‰øùËÆøÈóÆÊòØÂÆâÂÖ®ÁöÑ
  const secondCardObj = board[secondCard.value.row][secondCard.value.col]
  
  // @ts-expect-error Á°Æ‰øùfirstCardObjÂíåsecondCardObjÂ≠òÂú®
  const isMatch = firstCardObj.emoji === secondCardObj.emoji
  
  if (isMatch) {
    // ÂåπÈÖçÊàêÂäü
    // @ts-expect-error Á°Æ‰øùfirstCardObjÂíåsecondCardObjÂ≠òÂú®
    firstCardObj.isMatched = true
    // @ts-expect-error Á°Æ‰øùfirstCardObjÂíåsecondCardObjÂ≠òÂú®
    secondCardObj.isMatched = true
    matchedPairs.value++
    
    // ÈáçÁΩÆÈÄâÊã©
    resetSelection()
    
    // Ê£ÄÊü•Ê∏∏ÊàèÊòØÂê¶ÂÆåÊàê
    checkGameCompletion()
  } else {
    // ‰∏çÂåπÈÖçÔºåÁøªÂõûÂç°Áâá
    lockBoard.value = true
    setTimeout(() => {
      // @ts-expect-error Á°Æ‰øùfirstCardObjÂíåsecondCardObjÂ≠òÂú®
      firstCardObj.isFlipped = false
      // @ts-expect-error Á°Æ‰øùfirstCardObjÂíåsecondCardObjÂ≠òÂú®
      secondCardObj.isFlipped = false
      resetSelection()
      lockBoard.value = false
    }, 1000)
  }
}

// ÈáçÁΩÆÈÄâÊã©
const resetSelection = () => {
  firstCard.value = null
  secondCard.value = null
}

// Ê£ÄÊü•Ê∏∏ÊàèÊòØÂê¶ÂÆåÊàê
const checkGameCompletion = () => {
  if (matchedPairs.value === totalPairs.value) {
    gameCompleted.value = true
  }
}

// ÁªÑ‰ª∂ÊåÇËΩΩÊó∂ÂàùÂßãÂåñÊ∏∏Êàè
onMounted(() => {
  initGame()
})
</script>

